.Home {
  padding: 5px;
  display: flex;
  flex-direction: column;
  background-color: #222;
}

.Home .IncomDetailes {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: #222;
}

.Home .IncomDetailes > div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 25%;
  height: 150px;
  background-color: #333;
  border-radius: 5px;
  padding: 10px;
  -webkit-box-shadow: 4px 3px 10px 1px rgba(0, 0, 0, 0.24);
  box-shadow: 4px 3px 10px 1px rgba(0, 0, 0, 0.24);
}

.Home .IncomDetailes > div p {
  color: #999;
  font-size: 22px;
}

.Home .IncomDetailes > div h2 {
  color: #0f0;
  height: 100%;
  font-size: 30px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.Home .IncomDetailes > div h2 span {
  font-size: 22px;
  color: #999;
}

.Home .PrdouctsArea {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
}

.Home .PrdouctsArea > div {
  width: 40%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  background-color: #333;
  padding: 0 10px;
  border: 1px solid #555;
  border-radius: 5px;
  height: 400px;
  overflow-y: auto;
}

.Home .PrdouctsArea > div h3 {
  position: sticky;
  background-color: #333;
  top: 0;
  text-align: center;
  padding: 10px;
  margin-bottom: 5px;
  color: #999;
}

.Home .PrdouctsArea .HomeProduct {
  display: flex;
  width: 100%;
  height: 70px;
  margin-bottom: 5px;
  background-color: #444;
  border-radius: 2px;
  color: #ccc;
}

.PrdouctsArea .HomeProduct img {
  height: 100%;
  width: 70px;
  border-radius: 2px;
}

.PrdouctsArea .HomeProduct > P {
  word-break: break-all;
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin-right: 10px;
}

.PrdouctsArea .HomeProduct > span {
  height: 100%;
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #555;
  border-radius: 2px;
  font-size: 18px;
}
